import React, { Component } from 'react';
import Title from '../components/Title';
import Messages from '../components/Messages';
import Footer from '../components/Footer';


class App extends Component {
  constructor(){
    super()
    this.state = {
      content: null,      
    }
  }

  async componentDidMount(){
    const resp = await fetch('https://api.kanye.rest/')
    const data = await resp.json();
    this.setState({content: data.quote})
      
    }

  render (){
    return(
      <div>
       <div className='flex items-start justify-center pa5 tc'> 
        <div className='bg-washed-green mw5 h-auto pa3 br4 grow'>
          <Title />            
          <Messages message={this.state.content}/> 
        </div>                 
      </div>      
         <Footer />     
   </div>
      
  )
  
  };
}

export default App;
